.App {
  text-align: center;
}

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/

  height: 40vmin;
  pointer-events: none;
}

.App-Cover {
  background-color: rgba(0, 0, 0, 0.4);
  /*position: absolute;*/
  min-height: 100vh;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header {
  background-image: url("https://drive.google.com/uc?export=download&id=1abWzl57WBaSxw6Nd3ufcT3ofouH02HCJ");
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
